import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import { DataService } from '../dashboard/utility/data.service';
import { Student } from '../dashboard/common/student.model';
@Component({
  selector: 'app-graph',
  templateUrl: './graph.component.html',
  styleUrls: ['./graph.component.css']
})
export class GraphComponent implements OnInit {
  allStudentData: Student[] = [];
  public options: any = {
    chart: {
      backgroundColor: '#375A69',
      type: 'areaspline'
    },
    title: {
      text: ''
    },
    xAxis: {
      labels: {
        style: {
          color: '#C0D9E4'
        }
      },
      categories: [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
      ]
    },
    yAxis: {
      max: 100,
      min: 0,
      title: {
        text: ''
      },
      labels: {
        style: {
          color: '#C0D9E4'
        }
      },
      // plotLines: [{
      //   color: '#748c96',
      //   dashStyle: 'Dash',
      //   value: 3,
      //   width: 2
      // }]
    },
    credits: {
      enabled: false
    },
    plotOptions: {
      areaspline: {
        // fillOpacity: 0.3,
        lineWidth: 1,
       
        fillColor: {
          linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
          fillOpacity: 0.3,
          stops: [
            [0, '#FAD961'],
            [1, 'rgba(247,107,28,0) ']
          ]
        }
      },
  },
  series: [{
    color: '#f88f39',
    name: 'Attendence (%)',
    showInLegend: false,
    data: [3, 4, 3, 5, 4]
  }]
}
constructor(private dataService: DataService) { }

ngOnInit() {
  this.dataService.allStudentsData$.subscribe(data => {
    this.allStudentData = data;
    if (this.allStudentData.length) {
      this.formatAttendanceData();
      this.options.xAxis.categories = Object.keys(this.allStudentData[0].attendance);
    }
  });
}

formatAttendanceData() {
  let length = Object.keys(this.allStudentData[0].attendance).length;
  let sum = new Array(length).fill(0);
  this.allStudentData.map(student => {
    let i = 0;
    for (let month in student.attendance) {
      if (student.attendance.hasOwnProperty(month)) {
        sum[i] = sum[i] + Number(student.attendance[month].replace("%", ""));
        i++;
      }
    }
  });
  sum = sum.map(v => Number((v / this.allStudentData.length).toFixed(2)));
  this.options.series[0].data = sum;
  Highcharts.chart('graphContainer', this.options);
}

}
